import { useEffect, useState } from "react";
import Modal from "react-modal";
import {ABIStaking, addressStaking } from "../utils.js/stakeinfo";
import {ABIToken, addressToken } from "../utils.js/tokeninfo";
import { useAccount } from 'wagmi';
import { formatEther, parseUnits } from 'viem';
import { writeContract, readContract } from '@wagmi/core';
import image1 from '../images/inve1.png';

function getReadableTimeAndDate(solidityTime) {
    // Solidity time is in seconds, so we need to convert it to milliseconds first.
    const unixTime = solidityTime * 1000;
  
    // Create a new Date object from the Unix time.
    const date = new Date(unixTime);
  
    // Format the date using the toLocaleString() method.
    const readableTimeAndDate = date.toLocaleString();
  
    // Return the readable time and date.
    return readableTimeAndDate;
  }

function getCurrentTimeInEpochFormat() {
    // Get the current time in milliseconds.
    const currentTimeInMilliseconds = Date.now();

    // Divide the current time in milliseconds by 1000 to get the current time in seconds.
    const currentTimeInSeconds = currentTimeInMilliseconds / 1000;

    // Return the current time in seconds.
    return currentTimeInSeconds;
}


  const maturityPeriod = 7776000;
  const calmDownPeriod = 604800;

export function Stakecard() {
    const [stakeInfos, setStakeInfos] = useState([]);
    const [errors, setError] = useState('');
    const { address, isConnecting, isDisconnected, isConnected } = useAccount();
    const [isOpen, setIsOpen] = useState(false);

    const handleRequestClose = () => {
        setIsOpen(false);
      };
    
        

    async function claimReward(stakeId, i){
        if ((getCurrentTimeInEpochFormat() - parseInt(stakeInfos[i][3])) > calmDownPeriod){
            setError("Check wallet and approve transaction");
            setIsOpen(true);
            try {
            const { hash } = await writeContract({
                address: addressStaking,
                abi: ABIStaking,
                functionName: 'withdrawInterest',
                args: [stakeId],
            })
        }catch (error) {
            setError(error);
            setIsOpen(true);
            
          }}
          else{
            setError("Calm down period has not elapsed");
            setIsOpen(true);
          }
    }

    async function unstakeCapital(stakeId, i){
       if ((getCurrentTimeInEpochFormat() - parseInt(stakeInfos[i][2])) > maturityPeriod){
        setError("Check wallet and approve transaction");
            setIsOpen(true);
        try {
            const { hash } = await writeContract({
                address: addressStaking,
                abi: ABIStaking,
                functionName: 'withdrawCaptical',
                args: [stakeId],
            })
        }catch (error) {
            setError(error);
            setIsOpen(true);
         
          }}
          else{
            setError("Maturity period has not elapsed");
            setIsOpen(true);
          }
    }

   

    useEffect(() => {
        async function loadStakeInfo(){
            let stakingIds = await readContract({
                address: addressStaking,
                abi: ABIStaking,
                functionName: 'getStakingIds',
                args: [address],
                chainId: 56
            })
            let stakeInfo = [];
            for(let i=0; i < stakingIds.length; i++){
                let x = await readContract({
                    address: addressStaking,
                    abi: ABIStaking,
                    functionName: 'stakes',
                    args: [address, i],
                    chainId: 56
                })
                stakeInfo[i] = x;
            }
            setStakeInfos(stakeInfo)
        }
        if(isConnected){
        loadStakeInfo()
        
        }
        
    }, [!isConnected]);
  
    if(stakeInfos[0]){
   
    for(let i=0; i < stakeInfos.length; i++){
    return (
        <div className="col-lg-8 col-12 mt-3 mx-auto">
            <Modal
      closeTimeoutMS={100}
      isOpen={isOpen}
      onRequestClose={handleRequestClose}
      contentLabel="My Modal"
      className="Modal"
    >
                            <div class="custom-block custom-block-overlay">
                                <div class="d-flex flex-column h-100">
                                    <img src="images/businesswoman-using-tablet-analysis.jpg" class="custom-block-image img-fluid" alt="" />

                                    <div class="custom-block-overlay-text d-flex">
                                        <div>
                                            <h5 class="text-white mb-2">Error</h5>

                                            <p class="text-white">{errors.toString().split(':')[0]}</p>

                                            <button class="btn custom-btn mt-2 mt-lg-3" onClick={() => setIsOpen(false)}>Close</button>
                                        </div>

                                    </div>
                                    <div class="section-overlay"></div>
                            </div>
                        </div>
    </Modal>
                            <div className="custom-block custom-block-topics-listing bg-white shadow-lg mb-5">
                                <div className="d-flex">
                                    <img src={image1} className="custom-block-image img-fluid" alt="" />

                                    <div className="custom-block-topics-listing-info d-flex">
                                        <div>
                                            <h5 className="mb-2">Stake Id : {stakeInfos[i][0].toString()}</h5>
                                            <h5 className="mb-2">Capital : ${formatEther(stakeInfos[i][1])}</h5>

                                            <p className="mb-0">Time of investment : {getReadableTimeAndDate(stakeInfos[i][2].toString())}</p>
                                            <p className="mb-0">Next reward available at : {getReadableTimeAndDate((parseInt(stakeInfos[i][2]) + calmDownPeriod).toString())}</p>
                                                    <p className="mb-0">Capital available on : {getReadableTimeAndDate((parseInt(stakeInfos[i][3]) + maturityPeriod).toString())}</p>


                                            <button className="btn custom-btn mt-3 mt-lg-4 me-3" onClick={() => claimReward(stakeInfos[i][0].toString(), i)}>Claim Reward</button> 

                                            <button className="btn custom-btn mt-3 mt-lg-4" onClick={() => unstakeCapital(stakeInfos[i][0].toString(), i)}>Unstake Capital</button>
                                        </div>

                                       
                                    </div>
                                </div>
                            </div>
                        </div>
        
    );
  }
}
}

export function Newstake() {
    const [iAmount, setiAmount] = useState();
    const { address, isConnecting, isDisconnected, isConnected } = useAccount();
    const [errors, setError] = useState([]);
    const [isOpen, setIsOpen] = useState(false);

  const handleRequestClose = () => {
    setIsOpen(false);
  };

    const handleChange = (event) => {
        setiAmount(parseUnits(event.target.value, 18));
      };

      async function checkAllowance(address){
        let x = await readContract({
            address: addressToken,
            abi: ABIToken,
            functionName: 'allowance',
            args: [address, addressStaking],
            chainId: 56
        });
        return x;
    }

    async function approveUSDC(amount){
        setError("Check wallet and approve transaction");
            setIsOpen(true);
        try {
            const { hash } = await writeContract({
                address: addressToken,
                abi: ABIToken,
                functionName: 'approve',
                args: [address, amount],
            })
            stakenow(amount)
        }catch (error) {
            setError(error);
            setIsOpen(true);
          }
        }

    async function stakenow(amount){
        setError("Check wallet and approve transaction");
            setIsOpen(true);
        try {
            const { hash } = await writeContract({
                address: addressStaking,
                abi: ABIStaking,
                functionName: 'stakeTokens',
                args: [amount],
            })
            setError("Success");
            setIsOpen(true);
        }catch (error) {
            setError(error);
            setIsOpen(true);
          }
        
    }
    
    async function stake(amount){
        if (parseInt(formatEther(await checkAllowance(address))) > parseInt(formatEther(amount))){
            stakenow(amount)}
          else{
            approveUSDC(amount)
          }
    }

    return (
        <div>
       <Modal
      closeTimeoutMS={100}
      isOpen={isOpen}
      onRequestClose={handleRequestClose}
      contentLabel="My Modal"
      className="Modal"
    >
                            <div class="custom-block custom-block-overlay">
                                <div class="d-flex flex-column h-100">
                                    <img src="images/businesswoman-using-tablet-analysis.jpg" class="custom-block-image img-fluid" alt="" />

                                    <div class="custom-block-overlay-text d-flex">
                                        <div>
                                            <h5 class="text-white mb-2">Error</h5>

                                            <p class="text-white">{errors.toString().split(':')[0]}</p>

                                            <button class="btn custom-btn mt-2 mt-lg-3" onClick={() => setIsOpen(false)}>Close</button>
                                        </div>

                                    </div>
                                    <div class="section-overlay"></div>
                            </div>
                        </div>
    </Modal>
       <div className="stake-sections custom-form mt-4 pt-2 mb-lg-0 mb-5">
        
        <div className="input-group input-group-lg">
            <span className="input-group-text bi-currency-dollar" id="basic-addon1">
                
            </span>

            <input type="search" className="form-control" id="keyword" placeholder="Amount in USDC" aria-label="Search" onChange={handleChange}/>

            <button type="submit" className="form-control" onClick={() => stake(iAmount)}>Stake</button>
        </div>
    </div> 
    </div>
    
    );
  }
